und: var(--bg1);\n    position: absolute;\n    display: flex;\n    justify-content: center;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: 10;\n}\n\n#w3a-modal .w3a-modal__loader.w3a-modal__loader--hidden {\n    display: none;\n}\n\n#w3a-modal .w3a-modal__loader-content {\n    text-align: center;\n    margin-bottom: 80px;\n    position: relative;\n    display: flex;\n    flex-direction: column;\n}\n\n#w3a-modal .w3a-modal__loader-info {\n    flex-grow: 1;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    padding: 0 30px;\n}\n\n#w3a-modal .w3a-spinner-label {\n    margin-top: 10px;\n    font-size: 16px;\n    font-weight: 500;\n    color: #0364ff;\n}\n\n#w3a-modal .w3a-spinner-message {\n    margin-top: 10px;\n    font-size: 16px;\n}\n#w3a-modal .w3a-spinner-message:first-letter {\n    text-transform: capitalize;\n}\n#w3a-modal .w3a-spinner-message.w3a-spinner-message--error {\n    color: #fb4a61;\n}\n\n#w3a-modal button.w3a-logout {\n    background: none;\n    border: 0;\n    padding: 0;\n    display: inline-flex;\n    align-items: center;\n    margin-bottom: 30px;\n    cursor: pointer;\n    margin-top: 20px;\n    color: #0364ff;\n}\n\n#w3a-modal .w3a-spinner-power {\n    margin-top: auto;\n    font-size: 12px;\n    line-height: 1.2em;\n    color: #b7b8bd;\n}\n#w3a-modal .w3a-spinner-power > img {\n    height: 32px;\n    width: auto;\n}\n\n.w3a-spinner {\n    display: inline-block;\n    position: relative;\n    width: 80px;\n    height: 80px;\n}\n.w3a-spinner div {\n    box-sizing: border-box;\n    display: block;\n    position: absolute;\n    width: 64px;\n    height: 64px;\n    margin: 8px;\n    border: 6px solid #0364ff;\n    border-radius: 50%;\n    animation: w3a-spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;\n    border-color: #0364ff transparent transparent transparent;\n}\n.w3a-spinner div:nth-child(1) {\n    animation-delay: -0.45s;\n}\n.w3a-spinner div:nth-child(2) {\n    animation-delay: -0.3s;\n}\n.w3a-spinner div:nth-child(3) {\n    animation-delay: -0.15s;\n}\n.w3a-spinner.w3a-spinner--small {\n    width: 64px;\n    height: 64px;\n}\n.w3a-spinner.w3a-spinner--small div {\n    width: 48px;\n    height: 48px;\n    border: 4px solid #0364ff;\n    border-color: #0364ff transparent transparent transparent;\n}\n@keyframes w3a-spinner {\n    0% {\n        transform: rotate(0deg);\n    }\n    100% {\n        transform: rotate(360deg);\n    }\n}\n\n/* Header */\n#w3a-modal .w3a-header {\n    display: flex;\n    color: var(--text-color2);\n    align-items: center;\n}\n#w3a-modal .w3a-header__logo {\n    height: auto;\n    width: 40px;\n    margin-right: 16px;\n}\n#w3a-modal h1.w3a-header__title {\n    font-family: var(--text-header);\n    font-weight: 600;\n    font-size: 20px;\n    line-height: 1.5em;\n}\n#w3a-modal p.w3a-header__subtitle {\n    font-size: 14px;\n    line-height: 1.5em;\n    font-weight: 400;\n}\n#w3a-modal button.w3a-header__button {\n    cursor: pointer;\n    position: absolute;\n    background: none;\n    padding: 0;\n    border: 0;\n    top: 20px;\n    right: 26px;\n}\n\n/* BODY */\n#w3a-modal .w3a-group {\n    margin-bottom: 24px;\n}\n#w3a-modal .w3a-group:last-child {\n    margin-bottom: 0;\n}\n\n#w3a-modal .w3a-group.w3a-group--hidden,\n#w3a-modal .w3a-group.w3a-group--social-hidden,\n#w3a-modal .w3a-group.w3a-group--email-hidden,\n#w3a-modal .w3a-group.w3a-group--ext-wallet-hidden {\n    display: none;\n}\n\n#w3a-modal .w3a-group:not(.w3a-group--hidden):not(:last-child),\n#w3a-modal .w3a-group:not(.w3a-group--social-hidden):not(:last-child),\n#w3a-modal .w3a-group:not(.w3a-group--email-hidden):not(:last-child),\n#w3a-modal .w3a-group:not(.w3a-group--ext-wallet-hidden):not(:last-child) {\n    border-bottom: 0.5px solid #5c6c7f;\n    padding-bottom: 24px;\n}\n\n#w3a-modal h6.w3a-group__title {\n    font-family: var(--text-header);\n    font-weight: 400;\n    font-size: 14px;\n    line-height: 1.5em;\n    margin-bottom: 8px;\n}\n\n/* Adapter List */\n#w3a-modal ul.w3a-adapter-list {\n    display: flex;\n    align-items: center;\n    padding: 0;\n    margin: 0;\n    gap: 16px;\n    overflow-y: hidden;\n    flex-wrap: wrap;\n    margin-bottom: 16px;\n    max-height: 500px;\n    transition: max-height 0.4s ease-in;\n}\n\n#w3a-modal ul.w3a-adapter-list.w3a-adapter-list--shrink {\n    max-height: 48px;\n    transition: max-height 0.4s ease-out;\n}\n\n#w3a-modal ul.w3a-adapter-list.w3a-adapter-list--hidden {\n    display: none;\n}\n\n#w3a-modal li.w3a-adapter-item {\n    list-style: none;\n}\n\n#w3a-modal .w3a-adapter-item--hide {\n    display: none;\n}\n\n#w3a-modal .w3a-adapter-item__label {\n    font-size: 12px;\n    color: #5c6c7f;\n    text-align: center;\n    margin-top: 8px;\n    text-transform: capitalize;\n    position: absolute;\n    transform: translate(-6px);\n    width: 60px;\n}\n\n/* Buttons */\n#w3a-modal button.w3a-button {\n    background-color: #2f3136;\n    border: 1px solid #404145;\n    box-sizing: border-box;\n    box-shadow: 2px 2px 12px rgba(3, 100, 255, 0.05);\n    border-radius: 24px;\n    height: 48px;\n    width: 100%;\n    padding: 8px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    font-family: var(--text-body);\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    color: var(--text-color2);\n    cursor: pointer;\n}\n\n#w3a-modal button.w3a-button:hover {\n    background: #595857;\n}\n\n#w3a-modal button.w3a-button:active {\n    background: #6f717a;\n}\n\n#w3a-modal button.w3a-button:focus-visible {\n    outline: 1px solid #daf0ff;\n    outline-offset: -1px;\n}\n\n#w3a-modal button.w3a-button--icon {\n    width: 48px;\n}\n\n#w3a-modal button.w3a-button--left {\n    justify-content: start;\n    padding: 8px 16px;\n}\n\n#w3a-modal button.w3a-button--left > img {\n    height: 30px;\n    width: auto;\n}\n\n#w3a-modal button.w3a-button--left > div.w3a-button__name {\n    max-width: 180px;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    text-transform: capitalize;\n}\n\n#w3a-modal button.w3a-button--left > div.w3a-button__note {\n    margin-left: 8px;\n    color: #b7b8bd;\n    margin-left: auto;\n}\n\n#w3a-modal .w3a-button__image {\n    max-width: 100%;\n    max-height: 100%;\n    transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1), visibility 0s;\n}\n\n#w3a-modal button.w3a-button.w3a-button--rotate .w3a-button__image {\n    transform: rotate(180deg);\n}\n\n#w3a-modal .w3a-button--left .w3a-button__image {\n    margin-right: 12px;\n}\n\n#w3a-modal button.w3a-button-expand {\n    height: unset;\n    width: auto;\n    margin-left: auto;\n    font-size: 12px;\n    margin-top: 16px;\n    display: flex;\n    border: 8px;\n    color: var(--text-color2);\n    align-items: center;\n    cursor: pointer;\n    border-radius: 12px;\n    cursor: pointer;\n    padding: 0 10px 0 8px;\n    background: transparent;\n}\n\n#w3a-modal button.w3a-button-expand svg {\n    width: 12px;\n    height: auto;\n    margin-right: 4px;\n}\n\n#w3a-modal .w3a-external-toggle {\n    display: block;\n}\n\n#w3a-modal .w3a-external-toggle.w3a-external-toggle--hidden {\n    display: none;\n}\n\n#w3a-modal .w3a-external-container {\n    display: block;\n    margin-bottom: 34px;\n}\n\n#w3a-modal .w3a-external-container.w3a-external-container--hidden {\n    display: none;\n}\n\n#w3a-modal .w3a-external-group {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 12px;\n    margin-bottom: 16px;\n}\n\n#w3a-modal .w3a-external-group__left {\n    flex-grow: 1;\n}\n\n#w3a-modal button.w3a-external-back {\n    background: none;\n    border: 0;\n    padding: 0;\n    display: inline-flex;\n    align-items: center;\n    margin-bottom: 30px;\n    cursor: pointer;\n    color: var(--text-color1);\n}\n\n#w3a-modal .w3a-external-back:focus-visible {\n    outline: 1px solid #daf0ff;\n}\n\n#w3a-modal .w3a-external-back .w3a-group__title {\n    margin-bottom: 0;\n    margin-left: 5px;\n}\n\n#w3a-modal .w3a-external-loader {\n    display: flex;\n    justify-content: center;\n}\n\n#w3a-modal .w3a-wallet-connect {\n    display: block;\n    text-align: center;\n    margin-bottom: 16px;\n}\n\n#w3a-modal .w3a-wallet-connect.w3a-wallet-connect--hidden {\n    display: none;\n}\n\n#w3a-modal .w3a-wallet-connect__container {\n    padding: 10px;\n    background: #ffffff;\n    border-radius: 10px;\n    color: var(--text-color1);\n    font-size: 10px;\n    width: fit-content;\n    margin: auto;\n}\n\n.w3a-wallet-connect-qr {\n    width: 200px;\n    margin: 16px 16px;\n    padding: inherit;\n}\n\n#w3a-modal .w3a-wallet-connect__logo > img {\n    text-align: center;\n    width: 115px;\n    margin-bottom: 16px;\n}\n\n/* Text Field */\n#w3a-modal .w3a-text-field {\n    background: #393938;\n    border: 1px solid #27282d;\n    box-sizing: border-box;\n    box-shadow: inset 2px 2px 10px rgba(0, 0, 0, 0.4);\n    border-radius: 24px;\n    padding: 0 28px;\n    height: 48px;\n    width: 100%;\n    font-family: var(--text-body);\n    font-size: 16px;\n    color: var(--text-color2);\n    margin-bottom: 16px;\n}\n\n#w3a-modal .w3a-text-field:active {\n    background: #0f1222;\n}\n\n#w3a-modal .w3a-text-field:focus-visible {\n    outline: 1px solid #daf0ff;\n    outline-offset: -1px;\n}\n\n/* Footer Components */\n#w3a-modal .w3a-footer {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    font-size: 10px;\n    line-height: 150%;\n    color: var(--text-color2);\n}\n\n#w3a-modal .w3a-footer__links {\n    padding: 0;\n    margin: 0;\n}\n\n#w3a-modal .w3a-footer__links a {\n    color: var(--text-color1);\n    text-decoration: none;\n}\n\n#w3a-modal .w3a-footer__links a:focus-visible {\n    outline: 1px solid #daf0ff;\n}\n\n#w3a-modal .w3a-footer__links span {\n    margin: 0 2px;\n}\n\n#w3a-modal .w3a-footer__secured {\n    text-align: right;\n    color: #b7b8bd;\n}\n#w3a-modal .w3a-footer__secured > img {\n    height: 14px;\n    width: auto;\n}\n\n/* Loader Bridge */\n#w3a-modal .w3a-modal__loader-bridge {\n    display: flex;\n    margin-bottom: 14px;\n}\n\n#w3a-modal .w3a-modal__loader-bridge-message span {\n    text-transform: capitalize;\n}\n\n#w3a-modal .w3a-modal__loader-app-logo {\n    display: flex;\n    padding: 8px;\n}\n\n#w3a-modal .w3a-modal__loader-app-logo img {\n    width: 64px;\n    height: auto;\n}\n\n#w3a-modal .w3a-modal__loader-adapter img {\n    width: 84px;\n    height: auto;\n}\n\n#w3a-modal .w3a-modal__connector {\n    display: flex;\n    align-items: center;\n}\n\n.w3a-modal__connector-beat {\n    display: inline-block;\n    position: relative;\n    width: 80px;\n    height: 80px;\n}\n\n.w3a-modal__connector-beat div {\n    position: absolute;\n    top: 33px;\n    width: 13px;\n    height: 13px;\n    border-radius: 50%;\n    background: #808080;\n    animation-timing-function: cubic-bezier(0, 1, 1, 0);\n}\n\n.w3a-modal__connector-beat div:nth-child(1) {\n    left: 8px;\n    animation: beat1 2.4s infinite;\n}\n\n.w3a-modal__connector-beat div:nth-child(2) {\n    left: 8px;\n    animation: beat2 2.4s infinite;\n}\n\n.w3a-modal__connector-beat div:nth-child(3) {\n    left: 8px;\n    animation: beat3 2.4s infinite;\n}\n\n.w3a-modal__connector-beat div:nth-child(4) {\n    left: 32px;\n    animation: beat4 2.4s infinite;\n}\n\n.w3a-modal__connector-beat div:nth-child(5) {\n    left: 56px;\n    animation: beat5 2.4s infinite;\n}\n\n@keyframes beat1 {\n    0% {\n        transform: scale(0);\n    }\n\n    25% {\n        transform: scale(0);\n    }\n\n    50% {\n        transform: scale(1);\n    }\n\n    75% {\n        transform: scale(0);\n    }\n\n    100% {\n        transform: scale(0);\n    }\n}\n\n@keyframes beat2 {\n    0% {\n        transform: scale(0);\n    }\n\n    25% {\n        transform: scale(1);\n    }\n\n    50% {\n        transform: translate(24px, 0);\n    }\n\n    75% {\n        transform: translate(0, 0);\n    }\n\n    100% {\n        transform: translate(0, 0) scale(0);\n    }\n}\n\n@keyframes beat3 {\n    0% {\n        transform: translate(0, 0);\n    }\n\n    25% {\n        transform: translate(24px, 0);\n    }\n\n    50% {\n        transform: translate(48px, 0);\n    }\n\n    75% {\n        transform: translate(24px, 0);\n    }\n\n    100% {\n        transform: translate(0, 0);\n    }\n}\n\n@keyframes beat4 {\n    0% {\n        transform: translate(0, 0);\n    }\n\n    25% {\n        transform: translate(24px, 0);\n    }\n\n    50% {\n        transform: translate(24px, 0) scale(0);\n    }\n\n    75% {\n        transform: translate(24px, 0) scale(1);\n    }\n\n    100% {\n        transform: translate(0, 0);\n    }\n}\n\n@keyframes beat5 {\n    0% {\n        transform: scale(1);\n    }\n\n    25% {\n        transform: scale(0);\n    }\n\n    50% {\n        transform: scale(0);\n    }\n\n    75% {\n        transform: scale(0);\n    }\n\n    100% {\n        transform: scale(1);\n    }\n}\n\n/* LIGHT MODE */\n#w3a-modal.w3a-modal--light {\n    --bg1: #ffffff;\n    --bg2: #f9f9fb;\n    --text-color1: #a2a5b5;\n    --text-color2: #5c6c7f;\n}\n\n#w3a-modal .w3a-group:not(.w3a-group--hidden):not(:last-child),\n#w3a-modal .w3a-group:not(.w3a-group--social-hidden):not(:last-child),\n#w3a-modal .w3a-group:not(.w3a-group--email-hidden):not(:last-child),\n#w3a-modal .w3a-group:not(.w3a-group--ext-wallet-hidden):not(:last-child) {\n    border-bottom: 0.5px solid #b7b8bd;\n    padding-bottom: 24px;\n}\n\n#w3a-modal.w3a-modal--light button.w3a-button {\n    background-color: #ffffff;\n    border: 1px solid #f3f3f4;\n    box-shadow: none;\n    color: #b7b8bd;\n}\n\n#w3a-modal.w3a-modal--light button.w3a-button:focus-visible {\n    outline: 1px solid #0f1222;\n}\n\n#w3a-modal.w3a-modal--light .w3a-text-field {\n    background: #ffffff;\n    border: 1px solid #ffffff;\n    box-shadow: inset 2px 2px 10px rgba(0, 0, 0, 0.1);\n    color: #b7b8bd;\n}\n\n#w3a-modal.w3a-modal--light .w3a-text-field:active {\n    color: #0f1222;\n    outline: 1px solid #0f1222;\n}\n\n#w3a-modal.w3a-modal--light .w3a-text-field:focus-visible {\n    color: #0f1222;\n    outline: 1px solid #0f1222;\n}\n\n#w3a-modal.w3a-modal--light .w3a-footer__links a:focus-visible {\n    outline: 1px solid #0f1222;\n}\n\n#w3a-modal.w3a-modal--light .w3a-external-back:focus-visible {\n    outline: 1px solid #0f1222;\n}\n"],"sourceRoot":""}]);
